
import {mapGetters} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
name: '',
components: {
Cell,
XHeader,
XButton,
Group,
XInput,
Toast
},
data(){
return {
tradeInfo:{},
isWarn:false,
isSuccess:false,
warnText:'票号不能为空!',
successText:'',
person:0,
number: '',
isLoading: false,
ManualCheckInLoading:false,
list: [{
label: '出发站点',
value: '东莞'
}, {
label: '到达站点',
value: '日本'
}, {
label: '发车时间',
value: '2017-09-02 20:00发车'
},
{
label: '金额信息',
value: `共3张票 服务费0.01 金额 &nbsp;<i style="color: red;font-size: 18px">¥ 0.02</i>`
}],
buttons2: [{
style: 'primary',
text: '点击事件',
onButtonClick: (name) => {
alert(`clicking ${name}`)
}
}],
userInfo:{}
}
},
computed: mapGetters([
'userid',
'stationSource',
'openID'
]),
created(){
let tradeInfo =  JSON.parse(sessionStorage.getItem('tradeInfo'))
let userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
if(userInfo){
this.userInfo = userInfo
}else{
this.isWarn = true;
this.warnText = '请重新登录!'
return
}
if(tradeInfo){
this.tradeInfo = tradeInfo;
}else{
this.isWarn = true;
this.warnText = '车站编码不能为空!'
return
}
},
methods: {

ManualCheckIn(){
if(!this.number){
this.isWarn = true;
return
}
let options = {
"userID": "qianke",
"password": "qianke123",
"ticketid": this.number,
"openid": this.openID,
"stationID": this.stationSource,
"checkUserID":this.userInfo.UserID,
checkUserName:this.userInfo.UserName
};
this.ManualCheckInLoading = true;
this.$store.dispatch('initTicketDelivery',options)
.then(person=>{
this.ManualCheckInLoading = false;
this.person = person;
this.isSuccess = true;
this.successText = '检票成功!';
},err=>{
this.ManualCheckInLoading = false;
this.isWarn = true;
this.warnText = err;
})
},

sweepSubmit(){
this.isLoading = true;
let code = sessionStorage.getItem('code');
let openID = sessionStorage.getItem('openID');
let tradeInfo = JSON.parse(sessionStorage.getItem('tradeInfo'));
let options = {
"userID": "qianke",
"password": "qianke123",
"code": code,
"url": window.location.href,
"appid": tradeInfo.Appid,
"secret": tradeInfo.AppSecret
};
var _this = this;
this.$store.dispatch('sweepSubmit', options)
.then(data => {
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: tradeInfo.Appid, // 必填，公众号的唯一标识
timestamp: data.timestamp, // 必填，生成签名的时间戳
nonceStr: data.nonceStr, // 必填，生成签名的随机串
signature: data.signature,// 必填，签名，见附录1
jsApiList: ['scanQRCode']
});
wx.ready(function () {
_this.isLoading = false;

wx.scanQRCode({
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
success: function (res) {
var result = res.resultStr; 
if (result.indexOf(',') > 0) {
result = result.split(",")[1];
}
if (typeof (result) != "undefined") {
let deliveryOptions = {
"userID": "qianke",
"password": "qianke123",
"ticketid": result,
"openid": _this.openID,
"stationID": _this.stationSource,
"checkUserID":_this.userInfo.UserID,
checkUserName:_this.userInfo.UserName
};

_this.$store.dispatch('initTicketDelivery',deliveryOptions)
.then(person=>{
_this.person = person;
_this.isSuccess = true;
_this.successText = '检票成功!';
},err=>{
_this.isWarn = true;
_this.warnText = err;
})
}

}
});
});
wx.error(function (res) {

alert('配置有误' + res);
});
})
}
},
}



