
import {mapGetters} from 'vuex'
import Toast from 'vux/src/components/toast/index.vue'


export default {
name: '',
components:{
Toast
},
data() {
return {
options: {
"userID": "",
"password": "",
"stationID": "",
},
isWarn:false,
warnText:'请输入账号!',
tradeInfo:{}
}
},
created(){
let tradeInfo =  JSON.parse(sessionStorage.getItem('tradeInfo'))
if(tradeInfo){
this.tradeInfo = tradeInfo;
}else{
this.isWarn = true;
this.warnText = '车站编码不能为空!'
return
}
},
computed: mapGetters([]),
methods: {

login(){
if(!this.options.userID){
this.isWarn = true;
return
}
if(!this.options.password){
this.isWarn = true;
this.warnText='请输入密码!'
return
}
this.options.stationID = this.tradeInfo.Sid
this.$store.dispatch('Login',this.options)
.then((list)=>{
sessionStorage.setItem('userInfo',JSON.stringify(list))
this.$router.push({path: '/sweep'})
},err=>{
this.isWarn = true;
this.warnText=err
})
}
},
}



