import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Sweep from '@/components/Sweep'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/sweep',
      name: 'Sweep',
      component: Sweep
    }
  ]
})



